.comics-main {
    width: 100%;
    height: 100%;
    @include flexColumn;
    .color-button {
        position: absolute;
        top: .5em;
        right: 1vw;
    }
    h1 {
        margin: 2em 0;
    }
    .comics-container {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 90%;
    }
    .comic {
        @include flexColumn;
        position: relative;
        margin: .5em;
    }
    .image-container {
        width: 10em;
        height: 14em;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .modify-comic {
        cursor: pointer;
        position: absolute;
        top: -1em;
        right: -1vw;
        &::before {
            @include icon('\f044',1em);
        }
    }
}